//
// Component: Small Box
//

.small-box {
  @include border-radius($border-radius);
  @include box-shadow($card-box-shadow);

  display: block;
  margin-bottom: 20px;
  position: relative;

  // content wrapper
  > .inner {
    padding: 10px;
  }

  > .small-box-footer {
    background-color: rgba($black, .1);
    color: rgba($white, .8);
    display: block;
    padding: 3px 0;
    position: relative;
    text-align: center;
    text-decoration: none;
    z-index: 10;

    &:hover {
      background-color: rgba($black, .15);
      color: $white;
    }
  }

  h3 {
    @include font-size(2.2rem);
    font-weight: 700;
    margin: 0 0 10px;
    padding: 0;
    white-space: nowrap;
  }

  @include media-breakpoint-up(lg) {
    .col-xl-2 &,
    .col-lg-2 &,
    .col-md-2 & {
      h3 {
        @include font-size(1.6rem);
      }
    }

    .col-xl-3 &,
    .col-lg-3 &,
    .col-md-3 & {
      h3 {
        @include font-size(1.6rem);
      }
    }
  }

  @include media-breakpoint-up(xl) {
    .col-xl-2 &,
    .col-lg-2 &,
    .col-md-2 & {
      h3 {
        @include font-size(2.2rem);
      }
    }

    .col-xl-3 &,
    .col-lg-3 &,
    .col-md-3 & {
      h3 {
        @include font-size(2.2rem);
      }
    }
  }

  p {
    font-size: 1rem;

    > small {
      color: $gray-100;
      display: block;
      font-size: .9rem;
      margin-top: 5px;
    }
  }

  h3,
  p {
    z-index: 5;
  }

  // the icon
  .icon {
    color: rgba($black, .15);
    z-index: 0;

    > i {
      font-size: 90px;
      position: absolute;
      right: 15px;
      top: 15px;
      transition: transform 0.3s linear;

      &.fa,
      &.fas,
      &.far,
      &.fab,
      &.fal,
      &.fad,
      &.ion {
        font-size: 70px;
        top: 20px;
      }
    }

    svg {
      font-size: 70px;
      position: absolute;
      right: 15px;
      top: 15px;
      transition: transform 0.3s linear;
    }
  }

  // Small box hover state
  &:hover {
    text-decoration: none;

    // Animate icons on small box hover
    .icon {
      > i {
        &,
        &.fa,
        &.fas,
        &.far,
        &.fab,
        &.fal,
        &.fad,
        &.ion {
          transform: scale(1.1);
        }
      }
      > svg {
        transform: scale(1.1);
      }
    }
  }
}

@include media-breakpoint-down(sm) {
  // No need for icons on very small devices
  .small-box {
    text-align: center;

    .icon {
      display: none;
    }

    p {
      font-size: 12px;
    }
  }
}

// Overlays for Card, InfoBox & SmallBox
.card,
.overlay-wrapper,
.info-box,
.small-box {
  // Box overlay for LOADING STATE effect
  > .overlay,
  > .loading-img {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
  }

  .overlay {
    @include border-radius($border-radius);
    align-items: center;
    background-color: rgba($white, .7);
    display: flex;
    justify-content: center;
    z-index: 50;

    > .fa,
    > .fas,
    > .far,
    > .fab,
    > .fal,
    > .fad,
    > .svg-inline--fa,
    > .ion {
      color: $gray-800;
    }

    &.dark {
      background-color: rgba($black, .5);

      > .fa,
      > .fas,
      > .far,
      > .fab,
      > .fal,
      > .fad,
      > .svg-inline--fa,
      > .ion {
        color: $gray-400;
      }
    }
  }
}
